
.App {
  font-family: 'Raleway', sans-serif;
  color: rgb(211,211,211);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.students_container{
  padding-top: 5px;
  box-shadow: 1px 1px 1px 1px rgba(211,211,211,.4);
  border-radius: 20px;
  width: 60vw;
  height: 80vh;
  overflow: scroll;
  background-color: white;
}
.search_inputs{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.student_info{

  display: flex;
  align-items: flex-start;
  padding: 10px 0;

  p{  
   margin: 5px 0 5px 20px;
   color: rgba(0,0,0,.7);
   font-size: 16px;
  }
  form{
    margin: 15px 0 10px 20px;
  }
  h1{
    margin: 13px 0;
    color: black;
    font-size: 40px;
  }
  .student_description{
    margin-left: 20px;
  }
  div{
    margin-left: 40px;
  }
}
.profile_pic{
  height: 130px;
  border-radius: 50%;
  border: 1px solid rgba(211,211,211,.8);
  margin-left: 20px;
  margin-top: 20px
}

.student_filtering{
  width: 96%;
  height: 30px;
  padding: 10px;
  border: none;
  font-size: 20px;
  border-bottom: 2px solid rgba(211,211,211,.4);
}
input:focus {
  outline: none;
}
.tag_input{
  border: none;
  border-bottom: 2px solid rgba(211,211,211,.4);
  padding-bottom: 10px;
  font-size: 16px;
}
.student_tags{
  background-color: rgba(211,211,211,.7);
  display: inline-block;
  width: auto;
  padding: 5px;
  border-radius: 3px;
}
.expand{
  font-size: 60px;
  font-weight: bold;
  align-self: flex-start;
  margin: 0 !important;
  line-height: 50px;
  padding: 15px;
  cursor: pointer;
  background-color: white;
  border: none;
  color: rgba(0,0,0, .3);
}
.studentCard{
  display: flex;
  justify-content: space-between;
  align-content: flex-start;
  border-bottom: 1px solid rgba(211,211,211,.4);
}
.grade_collection{
  margin: 0 !important;
}
.ml-30{
  margin-left: 30px;
}
.grade_container{
  margin-top: 20px;
}

@media only screen and (max-width:1180px)  {
  .student_info{

    h1{
      font-size: 30px;
    }
  }
}
@media only screen and (max-width:920px)  {
  .student_info{

    h1{
      font-size: 15px;
    }
    p{
      font-size: 11px;
    }
  }
  .profile_pic{
    height: 100px;
  }
  .students_container{
    width: 90vw;
    height: 90vh;
  }
}
@media only screen and (max-width:430px)  {
  .students_container{
    width: 80vw;
  }
  .student_info {
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
  .studentCard  {
    flex-direction: column-reverse;
  }
  .student_info{
    p{
      margin-left: 0;
    }
    div{
      margin: 0;
      text-align: center;
    }
    form {
      margin: 10px 0
    }
  }
  .profile_pic {
    margin-left: 0;
    margin-bottom: 30px;
  }
  .expand{
    align-self: flex-end;
  }
  .tag_input{
    width: 70px;
    font-size: 11px;
    text-align: center;
    padding-left: 0;
  }
  .grade_container{
    padding-top: 20px;
  }
  .student_filtering {
    font-size: 14px;
  }
  .student_tags{
    margin-right: 5px !important;
  }
  .expand{
    line-height: 10px;
    padding-top: 20px;
  }
  
}